<template>
  <div class="container flex flex-dir-col flex-center">
    <el-card class="box-card m-tb-2 m-rl-auto p-2">
      <div class="flex">
        <!--  -->
        <div class="login-card">
          <h1>XSS Platform</h1>
          <span>Author:XDU Rick Shang</span>
          <el-form ref="form" :model="form">
            <el-form-item label="username">
              <el-input v-model="login_form.username"></el-input>
            </el-form-item>
            <el-form-item label="password">
              <el-input v-model="login_form.password" show-password></el-input>
            </el-form-item>
            <el-form-item>
              <!-- This is in the component you want to have the reCAPTCHA -->
              <vue-programmatic-invisible-google-recaptcha
                ref="invisibleRecaptcha1"
                :sitekey="config.ReCAPTCHA.sitekey"
                :elementId="'invisibleRecaptcha1'"
                :badgePosition="'left'"
                :showBadgeMobile="false"
                :showBadgeDesktop="false"
                @recaptcha-callback="recvLoginReCAPTCHAToken"
              ></vue-programmatic-invisible-google-recaptcha>
            </el-form-item>
            <el-form-item>
              <el-button type="primary" @click="onClickLoginBtn" id="grecaptcha"></el-button>
              <el-button @click="is_show_register_dialog=true"></el-button>
            </el-form-item>
          </el-form>
        </div>
        <!--  -->
        <Logo></Logo>
      </div>
    </el-card>
    <el-dialog width="25%" title="" :visible.sync="is_show_register_dialog">
      <register-form :form="register_form"></register-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="is_show_register_dialog=false"> </el-button>
        <el-button type="primary" @click="onClickRegisterBtn"> </el-button>
      </div>
      <!-- ReCAPTCHA -->
      <vue-programmatic-invisible-google-recaptcha
        ref="invisibleRigsterRecaptcha"
        :sitekey="config.ReCAPTCHA.sitekey"
        :elementId="'invisibleRigsterRecaptcha'"
        :badgePosition="'left'"
        :showBadgeMobile="false"
        :showBadgeDesktop="false"
        @recaptcha-callback="recvRegisterReCAPTCHAToken"
      ></vue-programmatic-invisible-google-recaptcha>
    </el-dialog>
  </div>
</template>

<script>
import Logo from "@/components/Logo";
import RegisterForm from "@/components/RegisterForm";
export default {
  components: {
    Logo,
    RegisterForm
  },
  data() {
    return {
      login_form: {
        username: "",
        password: "",
        g_recaptcha_response: ""
      },
      register_form: {
        password: "",
        repassword: "",
        g_recaptcha_response: ""
      },
      is_show_register_dialog: false
    };
  },
  methods: {
    //googletoken
    recvLoginReCAPTCHAToken(token) {
      this.login_form.g_recaptcha_response = token;
      this.login();
    },
    //ReCAPTCHA token
    recvRegisterReCAPTCHAToken(token) {
      this.register_form.g_recaptcha_response = token;
      this.register();
    },
    //
    login() {
      this.$axios({
        method: "POST",
        url: "/api/v1/login",
        data: this.login_form
      })
        .then(resp => {
          //1.token
          let token = resp.data.data;
          localStorage.setItem("user_token", token);
          //2.
          this.$router.push("/");
        })
        .catch(err => {
          console.log(err);
        });
    },
    //
    register() {
      this.$axios({
        method: "POST",
        url: "/api/v1/register",
        data: {
          password: this.register_form.password,
          g_recaptcha_response: this.register_form.g_recaptcha_response
        }
      })
        .then(resp => {
          //token
          let token = resp.data.data;
          localStorage.setItem("user_token", token);
          //
          let user_info = this.$parse.GetUserInfo(token);
          this.$alert(
            `${user_info.username},`,
            "",
            {}
          );
          //
          this.$router.push(`/`);
        })
        .catch(err => {
          console.log(err);
        });
    },
    //
    loginValidate() {
      if (this.login_form.username.length == 0) {
        this.$message({
          type: "warning",
          message: ""
        });
        return false;
      }
      if (this.login_form.password.length == 0) {
        this.$message({
          type: "warning",
          message: ""
        });
        return false;
      }
      return true;
    },
    //
    onClickLoginBtn() {
      //
      if (!this.loginValidate()) {
        return;
      }
      //reCAPTCHA ,token
      this.$refs.invisibleRecaptcha1.execute();
    },
    //
    registerValidate() {
      if (this.register_form.password.length == 0) {
        this.$message({
          type: "warning",
          message: ""
        });
        return false;
      }
      if (this.register_form.password != this.register_form.repassword) {
        this.$message({
          type: "warning",
          message: ""
        });
        return false;
      }
      return true;
    },
    //
    onClickRegisterBtn() {
      //
      if (!this.registerValidate()) {
        return;
      }
      //reCAPTCHA ,token
      this.$refs.invisibleRigsterRecaptcha.execute();
    }
  }
};
</script>

<style scoped>
.login-card {
  width: 40%;
  max-width: 400px;
  min-width: 250px;
  margin-right: 25px;
}
</style>>
