<template>
  <div>
    <el-button type="primary" size="small" @click="onClickCreateProjectBtn"></el-button>
    <el-table :data="projects" style="width: 100% border:0px">
      <el-table-column prop="id" label="#" width="50" type="index"></el-table-column>
      <el-table-column prop="name" label="" width="100">
        <template slot-scope="scope">
          <el-link type="warning" @click="onClickProjectName(scope.row)">{{scope.row.name}}</el-link>
        </template>
      </el-table-column>
      <el-table-column prop="desc" label=""></el-table-column>
      <el-table-column prop="loot_count" label=""></el-table-column>
      <el-table-column prop="created_at" label=""></el-table-column>
      <el-table-column prop="updated_at" label=""></el-table-column>
      <el-table-column fixed="right" label="" width="250">
        <template slot-scope="scope">
          <el-button @click="onClickUsageBtn(scope.row)" type="primary" size="small" plain> </el-button>
          <el-button @click="onClickConfigBtn(scope.row)" type="warning" size="small" plain> </el-button>
          <span style="margin-right:10px"></span>
          <el-popconfirm title="" @confirm="deleteProject(scope.row.id)">
            <el-button slot="reference" type="danger" size="small" plain></el-button>
          </el-popconfirm>
        </template>
      </el-table-column>
    </el-table>
    <!--  -->
    <el-dialog title="" :visible.sync="is_show_create_dialog">
      <ProjectForm :form="form"></ProjectForm>
      <div slot="footer" class="dialog-footer">
        <el-button @click="is_show_create_dialog=false"></el-button>
        <el-button type="primary" @click="createProject"></el-button>
      </div>
    </el-dialog>
    <!--  -->
    <el-dialog title="" :visible.sync="is_show_update_dialog">
      <ProjectForm :form="form"></ProjectForm>
      <div slot="footer" class="dialog-footer">
        <el-button @click="is_show_update_dialog=false"></el-button>
        <el-button type="primary" @click="updateProject"></el-button>
      </div>
    </el-dialog>
    <!--  -->
    <el-dialog title="" :visible.sync="is_show_usage_dialog">
      <usage :current_project="current_project"></usage>
    </el-dialog>
    <!--  -->
    <LootDrawer
      :project_name="current_project.name"
      :is_show_drawer="is_show_drawer"
      :loots="loots"
      @drawerClose="is_show_drawer = false"
    ></LootDrawer>
    <load-more-btn :loading="loading" :loadmore_btn_text="loadmore_btn_text" @loadmore="loadmore"></load-more-btn>
  </div>
</template>

<script>
import Usage from "@/components/Usage";
import ProjectForm from "@/components/ProjectForm";
import LootDrawer from "@/components/LootDrawer";
import LoadMoreBtn from "@/components/LoadMoreBtn";
export default {
  components: {
    Usage,
    ProjectForm,
    LootDrawer,
    LoadMoreBtn
  },
  data: () => {
    return {
      is_show_create_dialog: false,
      is_show_update_dialog: false,
      is_show_usage_dialog: false,
      is_show_drawer: false,
      form: {
        url_key: "",
        name: "",
        desc: "",
        module_list: []
      },
      projects: [],
      current_project: {},
      loots: [],
      offset: 0,
      count: 10,
      loadmore_btn_text: "",
      loading: false
    };
  },
  created() {
    this.loadmore_btn_text = this.config.loadmore_text;
    //
    this.getProjects();
  },
  methods: {
    //
    loadmore() {
      this.loading = true;
      this.getProjects();
    },
    //
    onClickUsageBtn(row) {
      this.is_show_usage_dialog = true;
      this.current_project = row;
    },
    //
    onClickCreateProjectBtn() {
      //1.
      this.is_show_create_dialog = true;
      //2.
      this.getProjectForm();
    },
    //
    onClickProjectName(row) {
      this.is_show_drawer = true;
      this.current_project = row;
      this.$axios({
        method: "GET",
        url: `/api/v1/loots/${row.url_key}`
      })
        .then(resp => {
          this.loots = resp.data.data;
        })
        .catch(err => {
          console.log(err);
        });
    },
    //
    onClickConfigBtn(row) {
      this.current_project = row;
      //1.
      this.is_show_update_dialog = true;
      //2.
      this.getProjectDetail(row.id);
    },
    //
    getProjectDetail(id) {
      this.$axios({
        method: "GET",
        url: `/api/v1/projectform?id=${id}`
      })
        .then(resp => {
          this.form = resp.data.data;
        })
        .catch(err => {
          console.log(err);
        });
    },
    //
    getProjects() {
      this.$axios({
        method: "GET",
        url: `/api/v1/projects/${this.offset}/${this.count}`
      })
        .then(resp => {
          let projects = resp.data.data == null ? [] : resp.data.data;
          for (let i = 0; i < projects.length; i++) {
            let index = this.projects.findIndex(item => {
              return item.id == projects[i].id;
            });
            if (-1 == index) {
              this.projects.push(projects[i]);
            } else {
              this.projects[index] = projects[i];
            }
          }
          this.loading = false;
          this.offset = this.projects.length;
          if (projects.length < this.count) {
            this.loadmore_btn_text = this.config.nomore_text;
          }
        })
        .catch(err => {
          console.log(err);
        });
    },
    //
    getProjectForm() {
      this.$axios({
        method: "GET",
        url: `/api/v1/projectform`
      })
        .then(resp => {
          this.form = resp.data.data;
        })
        .catch(err => {
          console.log(err);
        });
    },
    //
    updateProject() {
      this.$axios({
        method: "PUT",
        url: `/api/v1/project/${this.current_project.url_key}`,
        data: this.form
      })
        .then(resp => {
          //
          let i = this.projects.findIndex(item => {
            return item.url_key == resp.data.data.url_key;
          });
          this.projects[i].name = resp.data.data.name;
          this.projects[i].desc = resp.data.data.desc;
          this.projects[i].updated_at = "";
          //
          this.is_show_update_dialog = false;
          //
          this.$message({
            type: "success",
            message: ""
          });
        })
        .catch(err => {
          console.log(err);
        });
    },
    //
    createProject() {
      this.$axios({
        method: "POST",
        url: "/api/v1/project",
        data: this.form
      })
        .then(resp => {
          //
          this.projects.push(resp.data.data);
          //
          this.is_show_create_dialog = false;
          //
          this.$message({
            type: "success",
            message: ""
          });
        })
        .catch(err => {
          console.log(err);
        });
    },
    //
    deleteProject(id) {
      //
      this.$axios({
        method: "DELETE",
        url: `/api/v1/project/${id}`
      })
        .then(() => {
          this.$message({
            type: "success",
            message: ""
          });
          //
          let index = this.projects.findIndex(item => {
            return item.id == id;
          });
          this.projects.splice(index, 1);
        })
        .catch(err => {
          console.log(err);
        });
    }
  }
};
</script>