<template>
  <div style="height:100%" class="flex flex-dir-col">
    <el-header>
      <HeaderBar></HeaderBar>
    </el-header>
    <el-container>
      <el-aside>
        <NavBar :navbar="navbar"></NavBar>
      </el-aside>
      <el-main>
        <router-view></router-view>
      </el-main>
    </el-container>
    <el-footer>
      <i>{{config.foot_text}}</i>
    </el-footer>
  </div>
</template>

<script>
import NavBar from "@/components/NavBar";
import HeaderBar from "@/components/HeaderBar";
export default {
  components: {
    NavBar,
    HeaderBar
  },
  created() {
    //websocket
    let user_token = localStorage.getItem("user_token");
    this.conn = new WebSocket(
      `${this.config.ws.protocol}://${this.config.ws.domain}:${this.config.ws.port}/${this.config.ws.path}/${user_token}`
    );
    this.conn.onopen = this.onOpen;
    //websocket
    this.conn.onclose = this.onClose;
    //websocket
    this.conn.onmessage = this.onMessage;
  },
  beforeDestroy() {
    //
    this.closeWebsocket();
  },
  data: () => {
    return {
      conn: "",
      navbar: [
        {
          index: 1,
          title: "",
          to: "/",
          icon: "el-icon-files"
        },
        {
          index: 2,
          title: "",
          to: "/module",
          icon: "el-icon-star-off"
        },
        {
          index: 3,
          title: "",
          to: "/common_module",
          icon: "el-icon-toilet-paper"
        }
      ]
    };
  },
  methods: {
    //websocket
    onOpen(evt) {
      if ("development" == process.env.NODE_ENV) {
        this.$notify({
          message: "ws"
        });
        console.log(evt)
      }
    },
    //websocket
    onClose(evt) {
      if ("development" == process.env.NODE_ENV)
        this.$notify({
          message: "ws"
        });
        console.log(evt)
    },
    //
    onMessage(evt) {
      let loot = JSON.parse(evt.data);
      this.$notify.info({
        title: `${loot.url_key}+1`,
        message: this.$createElement(
          "i",
          { style: "word-break: break-all" },
          loot.content
        ),
        duration: 0
      });
    },
    //websocket
    closeWebsocket() {
      this.conn.close();
      if ("development" == process.env.NODE_ENV) {
        this.$notify({
          message: "ws"
        });
      }
    }
  }
};
</script>
