<template>
  <div>
    <el-button @click="is_show_add_module_dialog=true" size="small"></el-button>
    <el-dialog title="" :visible.sync="is_show_add_module_dialog">
      <ModuleForm :form="form" :disabled="false"></ModuleForm>
      <div slot="footer" class="dialog-footer">
        <el-button @click="is_show_add_module_dialog=false"> </el-button>
        <el-button type="primary" @click="addModuleHandler"> </el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import ModuleForm from "@/components/ModuleForm";
export default {
  props: ["add_module_url"],
  components: {
    ModuleForm
  },
  data: () => {
    return {
      is_show_add_module_dialog: false,
      form: {
        id: "",
        name: "",
        desc: "",
        xss_payload: "",
        is_common:true,
        param_list: [],
        option_list: []
      }
    };
  },
  methods: {
    //
    addModuleHandler() {
      this.$axios({
        method: "POST",
        url: this.add_module_url,
        data: this.form
      })
        .then(res => {
          this.$message({
            message: "",
            type: "success"
          });
          //
          let data = res.data.data;
          let module = {
            id: data.id,
            name: data.name,
            desc: data.desc,
            created_at: "",
            updated_at: "",
            username: data.username,
          };
          this.$emit("addModule", module);
          //
          this.is_show_add_module_dialog = false;
          //form
          this.form = {
            id: "",
            name: "",
            desc: "",
            xss_payload: "",
            param_list: [],
            option_list: [],
            is_common:true,
          };
        })
        .catch(err => {
          console.log(err);
        });
    }
  }
};
</script>